<template>
	<scroll-view 
		class="page-scroll" 
		:scroll-y="needScroll"
		:style="scrollStyle"
	>
		<view class="card-container" :style="{ paddingTop: navigationBarHeight + 50 + 'px' }">
			<view class="card">
				<view class="card-row">
					<view class="card-info" @tap="goToAddressBook('send')">
						<view class="name-phone">
							<text class="text name">{{ senderInfo.name || '请添加寄件人信息' }}</text>
							<text class="text phone" v-if="senderInfo.phone">{{ senderInfo.phone }}</text>
						</view>
						<view class="address-line">
							<text class="card-label">寄</text>
							<text class="text address" :class="{'placeholder': !senderInfo.address}">
								{{ senderInfo.address || '暂无寄件人信息，点击填写' }}
							</text>
						</view>
					</view>
					<view class="card-action send" @tap.stop="goToAddressBook('send')">
						<text>地址簿</text>
					</view>
				</view>
				<view class="divider"></view>
				<view class="card-row">
					<view class="card-info" @tap="goToAddressBook('receive')">
						<view class="name-phone">
							<text class="text name">{{ receiverInfo.name || '请添加收件人信息' }}</text>
							<text class="text phone" v-if="receiverInfo.phone">{{ receiverInfo.phone }}</text>
						</view>
						<view class="address-line">
							<text class="card-label receive">收</text>
							<text class="text address" :class="{'placeholder': !receiverInfo.address}">
								{{ receiverInfo.address || '暂无收件人信息，点击填写' }}
							</text>
						</view>
					</view>
					<view class="card-action receive" @tap.stop="goToAddressBook('receive')">
						<text>地址簿</text>
					</view>
				</view>
			</view>
			
			<!-- 物品信息卡片 -->
			<view class="card item-card" @tap="showItemPopup">
				<view class="item-row">
					<text class="item-label">物品信息</text>
					<view class="item-action">
						<text :class="{'placeholder-text': !selectedItemInfo, 'selected-text': selectedItemInfo}">
							{{ formattedItemInfo }}
						</text>
						<uni-icons type="right" size="16" color="#999999"></uni-icons>
					</view>
				</view>
			</view>
			
			<view class="card company-card">
				<text class="company-title">选择快递公司</text>
				<scroll-view class="company-scroll" scroll-x>
					<view class="company-list">
						<view class="company-item" :class="{ active: selectedCompany === 'sf' }" @tap="selectCompany('sf')">
							<text class="recommend-tag">推荐</text>
							<view class="company-header">
								<image class="company-logo" src="/static/icons/sf.png" mode="aspectFit"></image>
								<view class="company-main">
									<text class="company-name">顺丰快递</text>
									<view class="price-info">
										<text class="current-price">¥12.0<text class="price-unit">起</text></text>
										<text class="original-price">¥15.0</text>
									</view>
								</view>
							</view>
							<text class="weight-info">续重 8元/kg</text>
							<view v-if="selectedCompany === 'sf'" class="check-icon-wrapper">
								<text class="check-icon">✓</text>
							</view>
						</view>
						
						<view class="company-item" :class="{ active: selectedCompany === 'zt' }" @tap="selectCompany('zt')">
							<view class="company-header">
								<image class="company-logo" src="/static/icons/zt.png" mode="aspectFit"></image>
								<view class="company-main">
									<text class="company-name">中通快递</text>
									<view class="price-info">
										<text class="current-price">¥8.0<text class="price-unit">起</text></text>
										<text class="original-price">¥12.0</text>
									</view>
								</view>
							</view>
							<text class="weight-info">续重 5元/kg</text>
							<view v-if="selectedCompany === 'zt'" class="check-icon-wrapper">
								<text class="check-icon">✓</text>
							</view>
						</view>
					</view>
				</scroll-view>
			</view>
			
			<view class="card time-card">
				<view class="time-row">
					<text class="time-label">期望上门时间</text>
					<view class="time-action" @tap="showPickupTimePopup">
						<text :class="{'placeholder-text': !pickupTime, 'selected-text': pickupTime}">
							{{ pickupTime || '请选择上门时间' }}
						</text>
						<uni-icons type="right" size="16" color="#999999"></uni-icons>
					</view>
				</view>
				<view class="insurance-row" v-if="pickupTime">
					<text class="insurance-label">保费</text>
					<view class="insurance-info" @tap="showInsurancePopup">
						<text class="insurance-optional">选填</text>
						<uni-icons type="right" size="16" color="#999999"></uni-icons>
					</view>
				</view>
			</view>
		</view>
		
		<!-- 明细抽屉 -->
		<view class="detail-drawer" v-if="showDetail">
			<view class="drawer-mask" @tap="closeDetailDrawer"></view>
			<view class="drawer-content">
				<view class="drawer-header">
					<text class="drawer-title">明细</text>
					<uni-icons type="closeempty" size="20" color="#999" @tap="closeDetailDrawer"></uni-icons>
				</view>
				<view class="drawer-body">
					<view class="detail-item">
						<text>首重(1kg)</text>
						<text>¥{{ basePrice }}</text>
					</view>
					<view class="detail-item">
						<text>续重</text>
						<text>¥{{ extraPrice }}/kg × {{ extraWeight }}kg = ¥{{ totalExtraPrice }}</text>
					</view>
				</view>
			</view>
		</view>
		
		<!-- 底部结算导航 -->
		<view class="footer">
			<view class="footer-left">
				<view class="price-section">
					<text class="price-label">预计：</text>
					<text class="price-value">¥12.0</text>
					<view class="price-detail" @tap="toggleDetailDrawer">
						<text>明细</text>
						<uni-icons :type="showDetail ? 'top' : 'bottom'" size="12" color="#007AFF"></uni-icons>
					</view>
				</view>
				<view class="agreement">
					<checkbox :checked="isAgree" @tap="toggleAgree" color="#007AFF" style="transform:scale(0.7)"/>
					<text class="agreement-text">
						阅读并同意
						<text class="agreement-link">《快递服务协议》</text>
					</text>
				</view>
			</view>
			<view class="footer-right">
				<button class="submit-btn">立即下单</button>
			</view>
		</view>
		
		<!-- 使用全局保价弹出层组件 -->
		<InsurancePopup ref="insurancePopup" @confirm="onInsuranceConfirm" />
		
		<!-- 使用期望上门时间弹出层组件 -->
		<pickup-time-popup ref="pickupTimePopup" @confirm="onPickupTimeConfirm" />
		
		<!-- 使用新的物品选择弹出层组件 -->
		<item-popup ref="itemPopup" @confirm="onItemConfirm" />
	</scroll-view>
</template>

<script>
import deviceMixin from '@/mixins/device.js'
import InsurancePopup from './popups/insurance-popup.vue'
import PickupTimePopup from './popups/pickup-time-popup.vue'
import ItemPopup from './popups/item-popup.vue'

export default {
	name: 'ExpressNormal',
	props: {
		navHeight: {
			type: Number,
			default: 0
		}
	},
	mixins: [deviceMixin],
	components: {
		InsurancePopup,
			PickupTimePopup
	},
	data() {
		return {
			selectedCompany: '',
			isAgree: false,
			selectedType: '',
			weight: 1,
			remark: '',
			itemDetail: '',
			selectedItemInfo: '',
			pickupTime: '',
			declaredValue: '',
			insuranceFee: '1.00',
			agreeInsurance: false,
			senderInfo: {
				name: '',
				phone: '',
				address: ''
			},
			receiverInfo: {
				name: '',
				phone: '',
				address: ''
			},
			showDetail: false,
			basePrice: 12.0, // 首重价格
			extraPrice: 8.0, // 续重单价
			extraWeight: 0, // 续重重量
		}
	},
	mounted() {
		// 内容加载完成后检查是否需要滚动
		this.$nextTick(() => {
			this.checkNeedScroll()
		})
	},
	computed: {
		scrollStyle() {
			return {
				height: this.needScroll ? '100vh' : 'auto'
			}
		},
		containerStyle() {
			return {
				'margin-top': this.marginTopStyle
			}
		},
		// 判断是否可以点击确定按钮
		canConfirm() {
			return this.itemDetail.trim() !== '' || this.selectedType !== ''
		},
		// 格式化物品信息显示
		formattedItemInfo() {
			if (!this.selectedItemInfo) {
				return '请选择物品信息'
			}
			return `${this.selectedItemInfo}/${this.weight}kg`
		},
		// 判断是否可以确认保价
		canConfirmInsurance() {
			return this.declaredValue && this.agreeInsurance
		},
		// 计算续重总价
		totalExtraPrice() {
			return (this.extraPrice * this.extraWeight).toFixed(1)
		}
	},
	methods: {
		showItemPopup() {
			this.$refs.itemPopup.open()
		},
		onItemConfirm(data) {
			this.selectedItemInfo = data.itemDetail
			this.weight = data.weight
			// 计算上门时间
			this.calculatePickupTime()
		},
		selectCompany(company) {
			this.selectedCompany = company
		},
		decreaseWeight() {
			if (this.weight > 0.5) {
				this.weight -= 0.5
			}
		},
		increaseWeight() {
			this.weight += 0.5
		},
		showWeightTip() {
			// 显示不可寄物品提示
		},
		// 选择物品类型
		selectType(type) {
			this.selectedType = type
			this.itemDetail = type // 自动填充到输入框
		},
		// 计算上门时间
		calculatePickupTime() {
			const now = new Date()
			const currentHour = now.getHours()
			
			// 如果当前时间超过18:00，显示明天的时间
			if (currentHour >= 18) {
				const tomorrow = new Date(now.getTime() + 24 * 60 * 60 * 1000)
				this.pickupTime = `明天16:00-19:00`
			} else {
				this.pickupTime = `今天16:00-19:00`
			}
		},
		// 确认物品信息
		confirmItem() {
			if (!this.canConfirm) {
				return
			}
			// 更新显示的物品信息
			this.selectedItemInfo = this.itemDetail
			// 计算上门时间
			this.calculatePickupTime()
			this.closeItemPopup()
		},
		// 打开保价弹窗
		showInsurancePopup() {
			this.$refs.insurancePopup.open()
		},
		
		// 关闭保价弹窗
		closeInsurancePopup() {
			this.$refs.insurancePopup.close()
		},
		
		// 计算保费
		calculateFee() {
			if (!this.declaredValue) {
				this.insuranceFee = '0.00'
				return
			}
			const fee = parseFloat(this.declaredValue) * 0.005
			this.insuranceFee = fee.toFixed(2)
		},
		
		// 切换保价协议同意状态
		toggleInsuranceAgreement() {
			this.agreeInsurance = !this.agreeInsurance
		},
		
		// 显示保价协议
		showInsuranceAgreement() {
			// 显示保价协议的逻辑
		},
		
		// 确认保价
		confirmInsurance() {
			if (!this.canConfirmInsurance) return
			this.closeInsurancePopup()
		},
		
		onInsuranceConfirm(data) {
			// 处理保价确认后的数据
			console.log('保价信息：', data)
		},
		showPickupTimePopup() {
			this.$refs.pickupTimePopup.open()
		},
		onPickupTimeConfirm(time) {
			this.pickupTime = time
		},
		// 添加跳转到地址簿的方法
		goToAddressBook(type) {
			uni.navigateTo({
				url: `/pages/address/address?type=${type}`,
				events: {
					// 监听选择地址后的回调
					selectAddress: (address) => {
						console.log('选择的地址:', address)
						// 根据类型更新对应的地址信息
						if (address.type === 'send') {
							this.senderInfo = {
								name: address.name,
								phone: address.phone,
								address: address.address
							}
						} else if (address.type === 'receive') {
							this.receiverInfo = {
								name: address.name,
								phone: address.phone,
								address: address.address
							}
						}
					}
				}
			})
		},
		// 切换明细抽屉显示状态
		toggleDetailDrawer() {
			this.showDetail = !this.showDetail
		},
		
		// 隐藏明细抽屉 (重命名)
		closeDetailDrawer() {
			this.showDetail = false
		}
	}
}
</script>

<style>
	.page-scroll {
		flex: 1;
	}
	
	.card-container {
		padding: 0 20rpx;
		padding-bottom: 140rpx;
		position: relative;
		z-index: 1;
	}
	
	.card {
		background-color: #FFFFFF;
		border-radius: 16rpx;
		box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.1);
		padding: 30rpx;
		margin-bottom: 20rpx;
		height: 300rpx;
		display: flex;
		flex-direction: column;
	}
	
	.card-row {
		display: flex;
		align-items: flex-start;
		position: relative;
		padding: 10rpx 0;
		flex: 1;
	}
	
	.divider {
		height: 2rpx;
		background-color: #EEEEEE;
		margin: 0;
		width: 100%;
		flex-shrink: 0;
	}
	
	.card-info {
		flex: 1;
		min-width: 0;
		position: relative;
		padding: 10rpx 0;
		cursor: pointer;
	}
	
	.name-phone {
		display: flex;
		align-items: center;
		margin-bottom: 12rpx;
		padding-left: 70rpx;
	}
	
	.name-phone .text {
		font-size: 32rpx;
		color: #333333;
	}
	
	.name {
		font-weight: bold;
		margin-right: 20rpx;
	}
	
	.phone {
		color: #666666;
		font-weight: bold;
	}
	
	.address-line {
		display: flex;
		align-items: flex-start;
		position: relative;
	}
	
	.card-label {
		position: absolute;
		left: 0;
		top: -25rpx;
		font-size: 34rpx;
		font-weight: bold;
		color: #FFFFFF;
		width: 40rpx;
		height: 40rpx;
		text-align: center;
		line-height: 40rpx;
		background-color: #007AFF;
		border-radius: 6rpx;
	}
	
	.card-label.receive {
		background-color: #007AFF;
	}
	
	.address {
		font-size: 28rpx;
		color: #999999;
		width: 16em;
		word-wrap: break-word;
		white-space: pre-wrap;
		line-height: 1.4;
		padding-left: 70rpx;
	}
	
	.placeholder {
		color: #999999;
	}
	
	.card-action {
		font-size: 28rpx;
		color: #007AFF;
		font-weight: bold;
		padding-left: 30rpx;
		white-space: nowrap;
		position: relative;
		margin-top: 40rpx;
		border-left: 1px solid #EEEEEE;
		margin-left: 20rpx;
	}
	
	.card-action.send {
		margin-top: 35rpx;
	}
	
	.card-action.receive {
		margin-top: 35rpx;
	}
	
	.item-card {
		height: auto;
		padding: 30rpx;
	}
	
	.item-row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10rpx 0;
	}
	
	.item-label {
		font-size: 32rpx;
		color: #333333;
	}
	
	.item-action {
		display: flex;
		align-items: center;
	}
	
	.placeholder-text {
		font-size: 28rpx;
		color: #999999;
		margin-right: 10rpx;
	}
	
	.selected-text {
		font-size: 28rpx;
		color: #333333;
		font-weight: bold;
		margin-right: 10rpx;
	}
	
	.company-card {
		height: auto;
		padding: 30rpx;
	}
	
	.company-title {
		font-size: 32rpx;
		color: #333333;
		font-weight: bold;
		margin-bottom: 20rpx;
	}
	
	.company-scroll {
		width: 100%;
		white-space: nowrap;
	}
	
	.company-list {
		display: flex;
		padding: 10rpx 0;
		gap: 20rpx;
	}
	
	.company-item {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 300rpx;
		height: 200rpx;
		padding: 20rpx;
		border-radius: 12rpx;
		background-color: #FFFFFF;
		position: relative;
		flex-shrink: 0;
		border: 2rpx solid #EEEEEE;
	}
	
	.company-header {
		display: flex;
		align-items: center;
		height: 100%;
		padding: 4rpx 0;
	}
	
	.company-logo {
		width: 80rpx;
		height: 80rpx;
		margin-right: 20rpx;
		flex-shrink: 0;
	}
	
	.company-main {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
	}
	
	.company-name {
		font-size: 28rpx;
		color: #333333;
		font-weight: bold;
		margin-bottom: 8rpx;
	}
	
	.price-info {
		display: flex;
		align-items: center;
		gap: 8rpx;
	}
	
	.current-price {
		font-size: 32rpx;
		color: #FF4D4F;
		font-weight: bold;
	}
	
	.price-unit {
		font-size: 24rpx;
		font-weight: normal;
		margin-left: 2rpx;
	}
	
	.original-price {
		font-size: 24rpx;
		color: #999999;
		text-decoration: line-through;
	}
	
	.weight-info {
		font-size: 24rpx;
		color: #666666;
		margin-top: auto;
	}
	
	.recommend-tag {
		position: absolute;
		top: 0;
		left: 0;
		background-color: #007AFF;
		color: #FFFFFF;
		font-size: 22rpx;
		padding: 4rpx 12rpx;
		border-radius: 12rpx 0 12rpx 0;
		font-weight: 500;
	}
	
	.check-icon-wrapper {
		position: absolute;
		right: 16rpx;
		bottom: 16rpx;
		width: 40rpx;
		height: 40rpx;
		border-radius: 50%;
		background-color: #007AFF;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.check-icon {
		color: #FFFFFF;
		font-size: 24rpx;
		line-height: 1;
	}
	
	.company-item.active {
		border: 2rpx solid #007AFF;
		background-color: #FFFFFF;
	}
	
	.time-card {
		height: auto;
		padding: 30rpx;
	}
	
	.time-row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10rpx 0;
		margin-bottom: 0;
	}
	
	.insurance-row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10rpx 0;
		border-top: 1rpx solid #F5F5F5;
		margin-top: 10rpx;
	}
	
	
	.insurance-info {
		display: flex;
		align-items: center;
		gap: 12rpx;
	}
	
	.insurance-optional {
		font-size: 28rpx;
		color: #999999;
	}
	
	/* 底部结算导航样式 */
	.footer {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		height: 120rpx;
		background-color: #FFFFFF;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 30rpx;
		box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);
		z-index: 2;
	}
	
	.footer-left {
		flex: 1;
	}
	
	.price-section {
		display: flex;
		align-items: center;
		margin-bottom: 6rpx;
	}
	
	.price-label {
		font-size: 28rpx;
		color: #333333;
	}
	
	.price-value {
		font-size: 36rpx;
		color: #FF4D4F;
		font-weight: bold;
	}
	
	.price-detail {
		display: flex;
		align-items: center;
		font-size: 24rpx;
		color: #007AFF;
		margin-left: 12rpx;
		padding: 2rpx 8rpx;
	}
	
	.agreement {
		display: flex;
		align-items: center;
	}
	
	.agreement-text {
		font-size: 24rpx;
		color: #666666;
	}
	
	.agreement-link {
		color: #007AFF;
	}
	
	.footer-right {
		margin-left: 20rpx;
	}
	
	.submit-btn {
		background: linear-gradient(to right, #FF9500, #FF5E3A);
		color: #FFFFFF;
		font-size: 32rpx;
		font-weight: bold;
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 60rpx;
		border-radius: 40rpx;
		border: none;
	}
	
	/* 添加明细抽屉样式 */
	.detail-drawer {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		z-index: 1;
